}

/* Track */
::-webkit-scrollbar-track {
  background: #1f1f1f00; 
}

/* Handle */
::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background: rgba(41, 41, 41, 0.74);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}


